



















import Vue from 'vue';

export default Vue.extend({
  name: 'LoadIcon',
  props: {
    photo: {
      type: String,
      required: true,
    },
    teamname: {
      type: String,
      required: true,
    },
    description: {
      type: String,
      required: true,
    },
    lead: {
      type: String,
      required: true,
    },
    trait1: {
      type: String,
      required: true,
    },
    trait2: {
      type: String,
      required: true,
    },
    trait3: {
      type: String,
      required: true,
    },
  },
  methods: {
    getImgUrl() {
      return require(`@/assets/about/${this.photo}`);
    },
  },
});
